// app/catalog/search/page.tsx
"use client"
import * as React from "react"
import { Input } from "@/components/ui/input"

export default function Page() {
  const [q, setQ] = React.useState("")
  const [rows, setRows] = React.useState<any[]>([])
  async function run() {
    if (!q) return
    const res = await fetch(`/api/search?q=${encodeURIComponent(q)}`)
    const data = await res.json()
    setRows(data?.results ?? data?.articles ?? []) // afhankelijk van op
  }
  return (
    <div className="p-6 space-y-3">
      <div className="flex gap-2">
        <Input placeholder="Zoek artikel / nummerâ€¦" value={q} onChange={e => setQ(e.target.value)} onKeyDown={e => e.key==="Enter" && run()} />
        <button className="px-3 py-2 border rounded-xl" onClick={run}>Zoek</button>
      </div>
      <pre className="text-xs overflow-auto bg-muted/30 p-3 rounded-xl">{JSON.stringify(rows, null, 2)}</pre>
    </div>
  )
}
